$input-padding-y: 0.375rem !default;
$input-padding-x: 0.75rem !default;
$input-font-size: 1rem;
$input-font-weight: 400;
$input-line-height: 1.5;
$input-color: #212529;
$input-bg: white;
$input-border-width: 1px;
$input-border-color: #ced4da;
$input-border-radius: 0.25rem;
$input-box-shadow: inset 0 1px 2px rgba(black, 0.075) !default;
$input-transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out !default;
$input-focus-color: $input-color;
$input-focus-bg: $input-bg;
$input-focus-border-color: tint-color(blue, 50%);
$input-box-shadow: inset 0 1px 2px rgba(black, 0.075) !default;
$input-focus-box-shadow: 0 0 0 0.25rem rgba(blue, 0.25) !default;
$input-placeholder-color: #6c757d;

.form-control {
	display: block;
	width: 100%;
	padding: $input-padding-y $input-padding-x;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
		"Droid Sans", "Helvetica Neue", sans-serif;
	font-size: $input-font-size;
	font-weight: $input-font-weight;
	line-height: $input-line-height;
	color: $input-color;
	background-color: $input-bg;
	background-clip: padding-box;
	border: $input-border-width solid $input-border-color;
	appearance: none; // Fix appearance for date inputs in Safari

	// Note: This has no effect on <select>s in some browsers, due to the limited stylability of `<select>`s in CSS.
	border-radius: $input-border-radius;

	box-shadow: $input-box-shadow;
	transition: $input-transition;

	&[type="file"] {
		overflow: hidden; // prevent pseudo element button overlap

		&:not(:disabled):not([readonly]) {
			cursor: pointer;
		}
	}

	// Customize the `:focus` state to imitate native WebKit styles.
	&:focus {
		color: $input-focus-color;
		background-color: $input-focus-bg;
		border-color: $input-focus-border-color;
		outline: 0;
		box-shadow: $input-box-shadow;
	}

	// Placeholder
	&::placeholder {
		color: $input-placeholder-color;
		// Override Firefox's unusual default opacity; see https://github.com/twbs/bootstrap/pull/11526.
		opacity: 1;
	}
}

textarea {
	&.form-control {
		min-height: 25px;
	}
}

.btn {
	display: inline-block;
	font-weight: 400;
	line-height: 1.5;
	color: #212529;
	text-align: center;
	text-decoration: none;
	vertical-align: middle;
	cursor: pointer;
	-webkit-user-select: none;
	-moz-user-select: none;
	user-select: none;
	background-color: transparent;
	border: 1px solid transparent;
	padding: 0.375rem 0.75rem;
	font-size: 1rem;
	border-radius: 0.25rem;
	transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out,
		box-shadow 0.15s ease-in-out;
	margin-top: 1rem;
	width: 100%;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
		"Droid Sans", "Helvetica Neue", sans-serif;
}

.btn-primary {
	color: #fff;
	background-color: #0d6efd;
	border-color: #0d6efd;
}

.signup-content-parent {
	text-align: center;
}

.auth-left-wrapper {
	display: table;
	position: absolute;
	top: 75%;
	left: 25%;
	width: 100%;
	height: 100%;
	box-sizing: border-box;
	transform: translate(-50%, -50%);
	display: flex;
	justify-content: center;
	flex-direction: row;
}

.auth-right-wrapper {
	display: table;
	position: absolute;
	top: 75%;
	left: 75%;
	width: 100%;
	height: 100%;
	box-sizing: border-box;
	transform: translate(-50%, -50%);
	display: flex;
	justify-content: center;
	flex-direction: row;
}

.auth-middle {
	display: table-cell;
	vertical-align: middle;
	text-align: center;
	width: 90%;
	height: 100%;
}

.auth-inner {
	text-align: left;
	width: 50%;
	display: inline-block;
	margin-left: auto;
	margin-right: auto;
	background: #ffffff;
	box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
	padding: 20px 25px 20px 25px;
	border-radius: 15px;
	transition: all 0.3s;
}

.auth-wrapper .form-control:focus {
	border-color: #167bff;
	box-shadow: none;
}

.auth-wrapper h3 {
	text-align: center;
	margin: 0;
	line-height: 1;
	padding-bottom: 20px;
}
.auth-wrapper label {
	font-weight: bold;
}

.form-group {
	margin-top: 10px;
}

.onboarding-btn {
	width: 20% !important;
}
