.conversation {
	background-color: #fff;
	box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
	flex-grow: 1;
	position: relative;
	margin-left: 30px;
	margin-right: 30px;
}

.conversation-header {
	position: relative;
	padding-left: 16px;
	padding-top: 27px;
	margin-bottom: 30px;
	width: 100%;
	height: 70px;
	text-align: left;
	line-height: 0px;
	box-shadow: 0 4px 6px -3px rgba(0, 0, 0, 0.16);
	.name {
		color: black;
		position: absolute;
		left: 70px;
		top: 22px;
	}
	.company {
		font-size: 15px;
		position: absolute;
		left: 70px;
		top: 42px;
	}
	img {
		width: 50px;
		height: 50px;
		border-radius: 24px;
		position: absolute;
		left: 10px;
		top: 10px;
	}
}

#text-bar {
	position: absolute;
	padding-top: 7px;
	padding-left: 10px;
	width: 100%;
	bottom: 0px;
	left: 0px;
	resize: none;
	border: 0px;
	border-top: 1px solid black;
	border-radius: 15px;
	border-top-left-radius: 0px;
	border-top-right-radius: 0px;
	font-size: 17px;
	color: black;
}

#text-bar:focus {
	outline: none !important;
}

#conversation-history {
	position: relative;
	// border: 1px solid black;
	height: 70vh;
}

.conversation-section {
	padding-left: 40px;
	padding-right: 40px;
	text-align: left;
	// border: 1px solid black;
	width: 100%;
	.name {
		color: black;
		font-size: 17px;
		margin-bottom: 0px;
	}
	.message {
		color: #6c6c6c;
		font-size: 15px;
	}
}
