.action-image {
	width: 30px;
	height: 30px;
}

.action-buttons {
	bottom: 0;
	display: flex;
	flex-direction: row;
	justify-content: space-around;
	width: 100%;
}

.action-button {
	flex: 1;
	display: flex;
	justify-content: center;
}

.action-button-image {
	border: 0;
	background: none;
}

.action-button-hover-parent {
	cursor: pointer;
	width: 100%;
}

.action-button-hover-text {
	height: 10px;
}
